<template>
  <!-- Sidenav -->
  <Sidebar />
  <!-- Main content -->
  <div class="main-content" id="panel">
    <!-- Topnav -->
    <Topbar />
    <!-- Header -->
    <Header />
    <!-- Page content -->
    <div class="container-fluid mt--6">
      <div class="row">
        <div class="col-12">
          <div class="card-wrapper">
            <!-- Custom form validation -->
            <div class="card">
              <!-- Card header -->
              <div class="card-header">
                <h3 class="mb-0">Edit User</h3>
              </div>
              <!-- Card body -->
              <div class="card-body">
                <form @submit.prevent="submit" method="POST">
                  <div class="form-group">
                    <label class="form-control-label">Full Name</label> <small class="text-danger">*</small>
                    <input type="text" class="form-control" v-model="user.name" required>
                  </div>
                  <div class="form-group">
                    <label class="form-control-label">Email</label> <small class="text-danger">*</small>
                    <input type="email" class="form-control" v-model="user.email" required>
                  </div>
                  <div class="form-group">
                    <label class="form-control-label">Password</label>
                    <input type="password" class="form-control" v-model="user.password">
                  </div>
                  <button class="btn btn-primary" type="submit">Submit form</button>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- Footer -->
      <Footer />
    </div>
  </div>
</template>

<script>
import Sidebar from "@/components/admin/Sidebar.vue"
import Topbar from "@/components/admin/Topbar.vue"
import Header from "@/components/admin/Header.vue"
import Footer from "@/components/admin/Footer.vue"
import axios from "axios";
import authHeader from "@/service/auth-header";

export default {
  components: {
    Footer,
    Sidebar,
    Header,
    Topbar
  },
  mounted() {
    this.fetchData()
  },
  data: function () {
    return {
      user: {
        name: "",
        email: "",
        password: "",
      }
    };
  },
  methods: {
    submit() {
      axios.patch(`${process.env.VUE_APP_SERVICE_URL}/users/${this.$route.params.id}`, this.user, { headers: authHeader() })
          .then(response => {
            if(response.data.code === 200) {
              alert(response.data.status)
              this.$router.push({
                name: 'user'
              })
            }
          }).catch(error => {
        console.log(error.response.data.status)
      })
    },
    fetchData() {
      axios.get(`${process.env.VUE_APP_SERVICE_URL}/users/${this.$route.params.id}`, { headers: authHeader() }).then(response => {
        this.user = {
          name: response.data.data.name,
          email: response.data.data.email,
        };
      });
    }
  }
}
</script>